import { styled } from "@rp/react/theme"
import { Paper, TextField } from "@rp/react/web/mui/core"

export const DocumentTextContainer = styled("div")(({ theme }) => {
  const contentBlockDefaults = {
    fontWeight: 400,
    fontSize: theme.typography.body1.fontSize,
    lineHeight: "24px",
    margin: 0,
    marginTop: theme.spacing(1),
  }

  return {
    overflow: "hidden",
    height: "100%",
    width: "100%",

    "&>div": {
      maxHeight: "100%",
      width: "100%",
      display: "flex",
      flexDirection: "column",
      color: theme.palette.grey[200],
      overflowY: "auto",
      overflowX: "hidden",
      ...theme.scrollbarStyles,
    },
    // Content blocks
    // HEADING
    "& h3": {
      ...contentBlockDefaults,
      color: "#A4D9FF", // TODO: Add BDPrimary[100] to theme
    },

    // Add spacing on top of HEADINGS
    "& * + h3": {
      marginTop: theme.spacing(4),
    },

    // PARAGRAPH
    "& p": {
      ...contentBlockDefaults,
    },

    // But remove margin from paragraphs inside list items and tables
    "& li>p, & td>p": {
      marginTop: 0,
    },

    // TABLE
    "& table": {
      borderCollapse: "collapse",
      marginTop: theme.spacing(2),
    },
    "& td": {
      color: "white",
      background: theme.palette.grey[700],
      boxShadow: "inset 0px -1px 0px #515151",
      padding: theme.spacing(2),
    },

    // FOOTER
    "& h5": {
      ...contentBlockDefaults,
      fontSize: theme.typography.caption.fontSize,
    },

    // LISTS
    "& ul, & ol": {
      paddingInlineStart: theme.spacing(3),
    },
  }
})

